<template>
    <section class="content">
        <div class="map_detail">
            <div class="bg" :style="{ backgroundImage : `url(${items[num].repPhoto.photoid.imgpath})`}">
                <div class="item" v-if="itemShow">   
                    <button @click="$router.go(-1)" class="btn_back"> 뒤로가기</button>                     
                    <b>{{ items[num].title }}</b>
                    <p>{{ items[num].introduction }}</p>
                    <p>{{ items[num].roadaddress }}  <span v-if="items[num].postcode !== null" class="ico_postcode">{{ items[num].postcode }}</span></p>
                    <p>{{ items[num].region1cd.label }} > {{ items[num].region2cd.label }}</p>  
                    
                    <p>#{{ items[num].alltag }}</p>   
                    <p>#{{ items[num].tag }}</p>   


                    <p v-if="items[num].phoneno !== null">전화번호{{ items[num].phoneno }}</p>
                    <p></p>
                    <div>
                        <span @click="updataItem">좋아요</span>
                        <em>455</em>
                    </div>
                    <!-- <div>
                        <a href="https://map.kakao.com/link/search/" target="_blank" class="btn">카카오맵 보기</a> 
                    <div> -->
                </div>            
            </div>
        </div>
    </section>
</template>

<script>
export default {
    name : 'detail-vue',
    data() {
        return {
            num : 0,
            itemShow : false
        }
    },
    props:{ 
        items : Array,   
        index : Number,
        
    },
    mounted(){
        for(var  i = 0; i < this.items.length ; i++) {                
            if(this.$route.params.id == this.items[i].repPhoto.photoid.photoid){
                this.num = i;
            }
        }        
        this.itemShow =  true   
        
        
        
    },   
    updated(){
        this.updateItem();
    }, 
    methods : {
        updateItem(){  
            for(var  i = 0; i < this.items.length ; i++) {                
                if(this.$route.params.id == this.items[i].repPhoto.photoid.photoid){
                    this.num = i;
                }
            }        
            this.itemShow =  true 
            
        },
        
    },   

}
</script>